.App {
  text-align: left;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.description {
  max-width:80%
}

iframe {
  min-width: 850pt;
  width:100%;
  min-height:800px;
}

.tutorialSection {
  width:90%;
  margin-left:5%;
  margin-bottom:25px;
}
.arrow {
  display: inline-block;
  line-height: 2.5em;
  text-align: center;
  background: #333;
  color: white;
  font-size: 1em;
  width: 2.5em;
  transition: margin 200ms;
  margin-left: .75em;
}
.btn {
  display: inline-block;
  background: #e2e2e2;
  border: 0;
  line-height: 2.5em;
  padding: 0 0 0 1em;
  margin-bottom: 1em;
  outline: none;
  text-decoration: none;
  color: #666;
  
} 
    
 

.checkboxes {

  list-style: none;
  float: left;
}
.checkboxes li{
  margin-bottom: 2px;
}